@font-face {
    font-family: futura;
    src: url(../fonts/futura-pt-medium.otf);
  }
  @font-face {
    font-family: futura-light;
    src: url(../fonts/futura-pt-light.otf);
  }
  @font-face {
    font-family: futura-demi;
    src: url(../fonts/futura-pt-demibold.otf);
  }

#main_exp{
    display:grid;
    width:100%;
    -ms-flex: 1 1  ;
        flex: 1 1  ;
    height:50%;
    padding: 5% 10% ;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -ms-flex-direction:column;
        flex-direction:column;
    font-size:16px;
    overflow-y:scroll;
}
    #main_exp div{
        -ms-flex:0 1 auto;
            flex:0 1 auto;
        
    }
    #main_exp .colored{
        background-color:#c2f0ff;
        border-radius:1em;
        cursor:pointer;
        -webkit-transition:0.3s;
        -o-transition:0.3s;
        transition:0.3s;
        margin: 0.5em 0 ;
    }
    #main_exp .colored:hover{
        /*box-shadow:0 0px 10px 0px rgba(0,0,0,.15)*/
        -webkit-box-shadow: #9e9e9e 0px 1px 5px;
                box-shadow: #9e9e9e 0px 1px 5px;
    }
    #main_exp .descrip{     
        -webkit-box-sizing:border-box;     
                box-sizing:border-box;
    }
        #main_exp .descrip h3{
            font-size:1em;
            font-family:futura-light;
            margin-bottom:0em;
        }
        #main_exp .descrip p{
            font-family:futura-light;
           
            margin-top:0.5em;
            font-size:0.8em;
        }
        #main_exp .descrip h3 span{ 
            font-size:1em;
            font-family:futura;
        }
        #main_exp .descrip li{
            font-family:futura-light;
            text-align:justify;
        }
        #main_exp .descrip >div{
            overflow:hidden;
            -webkit-transition:0.5s;
            -o-transition:0.5s;
            transition:0.5s;
        }
        #main_exp .descrip div >span{
            padding:0.5em 1em;
            background-color:#ff34fe;
            border-radius:1em;
            color:white;
            font-size:0.8em;
            font-family:futura;
            display:inline-block;
            margin-bottom:0.5em;
            margin-right:0.5em;
        }

#everything{
    max-height:100vh;
}
@media only screen and (min-width: 1200px) {
    #main_exp{
        padding-left:15%;
        padding-right:15%;
    }
}


@media only screen and (max-width: 768px) {
    #main_exp{
        padding: 5%;
    }
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 500px) {
    #main_exp{
        padding-top:10%;
    }
}



